import 'bootstrap';
import './static/css/simple-line-icons.css';
import './scss/index.scss';
// import $ from 'jquery';
import AOS from 'aos';
import 'aos/dist/aos.css';
import 'scrollit/scrollIt.js';

import jcLogo from './static/img/junior-cabral-logo.png';
import jcLogoWhite from './static/img/junior-cabral-logo-white.png';

function setNavbarStyle(style) {
  const nav = $('.navbar.fixed-top');
  const logo = $('#logo');
  if (style === 'transparent') {
      nav.removeClass('scrolled');
      logo.attr('src', jcLogoWhite);
  } else { // white
      nav.addClass('scrolled');
      logo.attr('src', jcLogo);
  }
}

$(function () {
  // Scrolling
  $(document).scroll(function () {
    const nav = $('.navbar.fixed-top');
    const logo = $('#logo');
    const items = $('#navbar-items');
    if ($(this).scrollTop() > nav.height()) {
      nav.addClass('scrolled');
      logo.attr('src', jcLogo);
      items.removeClass('bg-dark');
    } else {
      nav.removeClass('scrolled');
      logo.attr('src', jcLogoWhite);
      items.addClass('bg-dark');
    }
  });

  $.scrollIt({
    topOffset: -56
  });

  // AOS
  AOS.init({
    duration: 800
  });

  // Testimonials form
  $("#testimonial-form-send").click(function (event) {
    event.preventDefault();
    var sendButton = $("#testimonial-form-send");
    if (sendButton.hasClass("disabled")) { // Prevents multiple submissions!
      return;
    }

    // Disables send button
    $("#testimonial-form-send").addClass("disabled");

    var makeAlert = function(type, msg) {
      return '<div class="alert alert-' + type + ' alert-dismissible fade show" role="alert">' + msg + '<button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>';
    };

    var getFormData = function() {
      var form = {};
      $.each($('#testimonial-form').serializeArray(), function(i, field) {
        form[field.name] = field.value;
      });

      return {
        name: form.name,
        email: form.email,
        company: form.company,
        message: form.message
      };
    };

    var formData = getFormData();

    $.ajax('https://api.juniorcabral.com/testimonials', {
      data: JSON.stringify(formData),
      contentType: 'application/json',
      type: 'POST'
    })
      .done(function() {
        $("send-testimonial").modal("hide");
        var bsAlert = makeAlert("success", "Obrigado por enviar seu depoimento!");
        $("#testimonial-modal-trigger").before(bsAlert);
      })
      .fail(function() {
        var bsAlert = makeAlert("danger", "Ocorreu um erro ao enviar seu depoimento. Por favor, verifique os campos e tente novamente.");
        $("#testimonial-form").after(bsAlert);
      })
      .always(function() {
        $("#testimonial-form-send").removeClass("disabled");
      });
  });

  // Contact form
  $("#contact-form").submit(function (event) {
    event.preventDefault();
    var sendButton = $("#send");
    if (sendButton.hasClass("disabled")) { // Prevents multiple submissions!
      return;
    }

    // Disables send button
    $("#send").addClass("disabled");

    var makeAlert = function(type, msg) {
      return '<div class="alert alert-' + type + ' alert-dismissible fade show" role="alert">' + msg + '<button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>';
    };

    var getFormData = function() {
      var form = {};
      $.each($('#contact-form').serializeArray(), function(i, field) {
        form[field.name] = field.value;
      });

      return {
        name: form.name,
        email: form.email,
        message: form.message
      };
    };

    var formData = getFormData();

    $.ajax('https://api.juniorcabral.com/contato', {
      data: JSON.stringify(formData),
      contentType: 'application/json',
      type: 'POST'
    })
      .done(function() {
        var bsAlert = makeAlert("success", "Mensagem enviada com sucesso!");
        $('#send').before(bsAlert);
      })
      .fail(function() {
        var bsAlert = makeAlert("danger", "Ocorreu um erro ao enviar sua mensagem. Por favor, verifique os campos e tente novamente.");
        $('#send').before(bsAlert);
      })
      .always(function() {
        $("#send").removeClass("disabled");
      });
  });

});

